.footer {
	height: 45px;
}

.footer a {
	background: #45c8a6;
	color: white;
	text-align: center;
}
.footer a:nth-child(1){
	border-right: 1px solid #FFFFFF;
	box-sizing: border-box;
	line-height: 45px;
}

.footer a span {
	display: inline-block;
	color: white;
	height: 45px;
	line-height: 45px;
	font-size: 17px;
}
.footer a .iconfont{
	font-size: 26px;
	line-height: 45px;
}
.footer a:nth-child(2) .iconfont{
	line-height: 45px;
	transform: translateY(3px);
}
/*手表列表*/
.watchList{
	width: 100%;
	height: 35px;
	clear: both;
	display: flex;
	position: relative;
}
div.watchList_item{
	flex: 3;
	border-bottom: 1px solid #e5e5e5;
}
div.watchList_item a{
	display: block;
	width: 100%;
	height: 35px;
	line-height: 35px;
	font-size: 14px;
	color: #333333;
}
div.watchList_item.active{
	border-bottom: 2px solid #45c8a6;
}
.watchList>div.more{
	border-bottom: 1px solid #e5e5e5;
	width: 35px;
	padding-right: 10px;

}
.watchList .isMore{
	position: absolute;
	width: 120px;
	min-height: 30px;
	top: 35px;
	right: 0;
	border-left: 1px solid #e5e5e5;
	background: white;
}
.watchList>div.more .iconfont{
	font-size: 25px;
	line-height: 37px;
	color: #45c8a6;
}
/*健康分类*/
.health{
	width: 100%;
	padding-left: 10px;
	padding-right: 10px;
	background: white;
	clear: both;
	padding-top: 15px;
}
.healthList{
	width: 100%;
	min-height: 80px;
	background: #f0f0f0;
	border-radius: 10px;
	padding-left: 15px;
	padding-right: 15px;
}
.healthList_top_wrap{
	width: 100%;
	height: 70px;
	padding-top: 15px;
	border-bottom: 1px solid #E5E5E5;
	box-sizing: border-box;
}
.healthList_top{
	width: 100%;
	height: 40px;
}
.healthList_top div{
	height: 40px;
	float: left;
}
.healthList_top div.div_1{
	width: 40px;
	margin-right: 5px;
}
.healthList_top div.div_1 .iconfont{
	width: 40px;
	height: 40px;
	line-height: 40px;
	font-size: 38px;
	padding: 0;
	color: #45C8A6;
}
.healthList_top div.div_2 p{
	margin: 0;
	padding: 0;
}
.healthList_top div.div_2 p.p_1{
	height: 24px;
	font-size: 24px;
}
.healthList_top div.div_2 p.p_1 .iconfont{
	margin-left: 5px;
	font-size: 22px;
	color: #45C8A6;
}
.healthList_top div.div_2 p.p_2{
	height: 16px;
	font-size: 10px;
	line-height: 16px;
	text-align: left;
}
.healthList_top div.div_3{
	float: right;
}
.healthList_top div.div_3 p{
	height: 20px;
	line-height: 20px;
	margin: 0;
	padding: 0;
	font-size: 10px;
}
.healthList_top div.div_3 p.p_1{
	color: #45C8A6;
}
.healthList_top div.div_3 p.p_2{
	font-size: 10px;
}
.healthCenter{
	width: 100%;
	height: 255px;
	margin-top: 5px;
	padding-bottom: 10px;
}
.healthCenter_top{
	width: 100%;
	height: 40px;
	display: flex;
	background: white;
	border-bottom: 1px solid #E5E5E5;
	box-sizing: border-box;
}
.healthCenter_top span{
	flex: 1;
	height: 40px;
	display: block;
	line-height: 40px;
	text-align: center;
}
.healthCenter_top span:nth-child(1){
	border-right: 1px solid #E5E5E5;
	box-sizing: border-box;
}
.healthCenter ul{
	display: block;
	width: 100%;
	height: 205px;
	list-style: none;
	padding: 0;
	margin: 0;
	display: -webkit-flex; /* Safari */
  	display: flex;
  	flex-direction: column;
  	background: white;
}
.healthCenter ul li{
	height: 40px;
	flex: 1;
	display: flex;
  	flex-direction: row;
	border-bottom: 1px solid #E5E5E5;
	line-height: 100%;
}
.healthCenter ul li span{
	flex: 1;
	text-align: center;
	justify-content:center;
    align-items:center;
    height: 40px;
    line-height: 40px;
}
.healthCenter.jibu ul li span{
	flex: 1;
	text-align: center;
	justify-content:center;
    align-items:center;
    height: 205px;
    line-height: 205px;
}
.healthCenter ul li span:nth-child(1){
	border-right: 1px solid #E5E5E5;
	box-sizing: border-box;
}
.tabList{
	width: 100%;
	display: flex;
	height: 85px;
	flex-direction: row;
	border-radius: 10px;
	overflow: hidden;
	margin-top: 24px;
	padding-left: 10px;
	padding-right: 10px;
}
.tabList a{
	flex: 1;
	height: 85px;
}
.tabList a.active{
	border-radius: 10px;
	background: #45C8A6;
}
.tabList a.active span{
	color: white!important;
}
.tabList a span{
	width: 100%;
	display: block;
	text-align: center;
	color: #333333;
	font-weight: 500;
}
.tabList a span:nth-child(1){
	height: 53px;
	line-height: 53px;
	font-size: 30px;
}
.tabList a span:nth-child(2){
	height: 32px;
}
.tabList a:nth-child(1) .iconfont{
	color: #45C8A6;
}
.tabList a:nth-child(2) .iconfont{
	color: #37A3F1;
}
.tabList a:nth-child(3) .iconfont{
	color: #F45D7A;
}


